:root {
  --margin: 30px;
  --padding: 12px;
}

@media screen and (min-width: 991px) {
  :root {
    --padding: 15px;
  }
}
@media screen and (min-width: 1200px) {
  :root {
    --margin: 50px;
  }
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.content-box-component {
  box-sizing: content-box;
}

body {
  display: flex;
  flex-direction: column;
  background: #fff;
  color: #393939;
  font-family: "Fira Sans", "Helvetica", Arial, sans-serif;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body.no-scroll {
  overflow: hidden;
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  padding: 0;
  cursor: pointer;
  background: transparent;
  border: none;
  color: inherit;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

p {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  color: #000;
}

h2 {
  margin-bottom: 14px;
  font-weight: 500;
  font-size: 18px;
  line-height: 125%;
}

h3 {
  margin-bottom: 14px;
  font-weight: 500;
  font-size: 16px;
  line-height: 125%;
}

@media screen and (min-width: 1200px) {
  h2 {
    margin-bottom: 22px;
    font-size: 22px;
    line-height: 130%;
  }
  h3 {
    margin-bottom: 22px;
    font-size: 19px;
    line-height: 130%;
  }
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}

picture {
  display: block;
}

.background-image {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
}
.background-image picture {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.background-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.wrapper {
  width: 100%;
  max-width: 1480px;
  margin: 0 auto;
}

.container {
  width: 100%;
  max-width: 1230px;
  margin: 0 auto;
  padding: 0 var(--padding);
}

.main-title {
  font-weight: 500;
  font-size: 22px;
  line-height: 120%;
  color: #fff;
}

.title {
  margin-bottom: 14px;
  font-weight: 500;
  font-size: 18px;
  line-height: 125%;
}

.sub-title {
  margin-bottom: 14px;
  font-weight: 500;
  font-size: 16px;
  line-height: 125%;
}

.text {
  margin-bottom: var(--margin);
  font-weight: 400;
  font-size: 15px;
  line-height: 138%;
}
.text b {
  font-weight: 500;
}

.btn {
  width: 100%;
  padding: 12px 30px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background: #D82816;
  box-shadow: 0px 5px 10px rgba(216, 40, 22, 0.4);
  border-radius: 40px;
  font-weight: 400;
  font-size: 16px;
  line-height: 122%;
  text-align: center;
  color: #fff;
  transition: all 0.2s ease;
}
.btn:hover, .btn:focus {
  background: #C62F1F;
  box-shadow: none;
}
.btn--secondary {
  background: #fff;
  border-bottom: none;
  color: #416AA8;
  font-size: 12px;
  line-height: 143%;
  box-shadow: 0px 5px 10px rgba(65, 106, 168, 0.5);
}
.btn--secondary:hover, .btn--secondary:focus {
  background: #fff;
}
.btn--wide {
  padding: 12px 20px;
  font-size: 12px;
  line-height: 143%;
  border-bottom: 2px solid #A31212;
}

.info {
  margin-bottom: var(--margin);
}
.info .num-list {
  margin-bottom: 20px;
}

.num-list {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 15px;
  line-height: 120%;
  list-style-type: none;
  counter-reset: list1;
}
.num-list li {
  margin-bottom: 10px;
  padding-left: 20px;
  position: relative;
  list-style-type: none;
}
.num-list li:last-child {
  margin-bottom: 0;
}
.num-list li::before {
  font-weight: 500;
  position: absolute;
  top: 0;
  left: 0;
  width: 22px;
  text-align: left;
  color: #D82816;
  font-size: 16px;
  counter-increment: list1;
  content: counter(list1) ".";
}
.num-list ol {
  margin-top: 10px;
  padding: 0;
  padding-left: 10px;
  counter-reset: list2;
}
.num-list ol li {
  padding-left: 32px;
  margin-bottom: 15px;
}
.num-list ol li::before {
  width: 32px;
  counter-increment: list2;
  content: counter(list1) "." counter(list2) ".";
}

.dot-list {
  margin-bottom: 20px;
  padding-left: 20px;
  padding-right: 10px;
  font-weight: 400;
  font-size: 15px;
  line-height: 138%;
}
.dot-list li {
  margin-bottom: 10px;
  padding-left: 20px;
  position: relative;
}
.dot-list li::before {
  content: "";
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: #D82816;
  position: absolute;
  left: 0;
  top: 5px;
}

.important {
  margin-bottom: var(--margin);
  padding: 20px;
  padding-left: 15px;
  font-weight: 400;
  font-size: 15px;
  line-height: 138%;
  color: #393939;
  background: #FFF8EC;
  border-left: 5px solid #F0E7D8;
}
.important strong {
  font-weight: 500;
}

.select2 {
  width: 100% !important;
  height: 46px;
}
.select2 .selection {
  display: block;
  height: 100%;
}
.select2-container--below .select2-selection__rendered {
  padding: 8px 36px 10px 16px !important;
  border: 1px solid #8A8A8A !important;
  border-radius: 4px !important;
}
.select2-container--below .select2-selection--single {
  border: none;
}
.select2-selection {
  display: block;
  height: 100% !important;
  border-radius: 4px !important;
}
.select2-selection__rendered {
  width: 100%;
  height: 100% !important;
  padding: 10px 36px 10px 16px !important;
  font-weight: 400;
  font-size: 14px !important;
  line-height: 16px;
  color: #393939 !important;
}
.select2-selection__placeholder {
  padding: 10px 36px 10px 16px !important;
  display: block !important;
  height: 46px !important;
  line-height: 26px !important;
  font-size: 14px;
  color: #9F9F9F;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}
.select2-selection__arrow {
  width: 18px !important;
  height: 18px !important;
  top: 50% !important;
  right: 16px !important;
  background: url("../images/icons/arrow-down.svg") no-repeat center/contain;
  transform: rotate(180deg) translateY(50%);
}
.select2-selection__arrow b {
  display: none;
}
.select2-search__field {
  display: none !important;
}
.select2-search--dropdown {
  padding: 0 !important;
}
.select2-results__option {
  height: 45px;
  padding: 10px 18px;
  font-weight: 400;
  font-size: 15px;
  line-height: 25px;
  color: #393939 !important;
  background: #fff !important;
}
.select2-results__option:hover, .select2-results__option:focus {
  background: #E0E0E0 !important;
}
.select2-results__option--selected {
  background: #EBEBEB !important;
  color: #fff !important;
}
.select2-results__option--selectable {
  color: #393939 !important;
}

.overlay {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.3);
  display: none;
}
.overlay--active {
  display: block;
}

@media screen and (min-width: 360px) {
  .btn {
    padding: 14px 40px;
    font-size: 18px;
  }
  .btn--secondary {
    font-size: 14px;
  }
  .btn--wide {
    font-size: 14px;
  }
}
@media screen and (min-width: 768px) {
  .main-title {
    font-size: 30px;
  }
  .btn {
    width: auto;
    padding: 14px 70px 15px;
  }
  .btn--wide {
    max-width: 390px;
    padding: 14px 30px 15px;
  }
  .btn--secondary, .btn--wide {
    font-size: 16px;
  }
  .important {
    padding: 30px;
    padding-left: 25px;
  }
}
@media screen and (min-width: 1200px) {
  .main-title {
    font-size: 38px;
  }
  .num-list {
    font-size: 17px;
  }
  .num-list li {
    margin-bottom: 19px;
  }
  .num-list ol {
    margin-top: 14px;
  }
  .text {
    font-size: 17px;
    line-height: 167%;
  }
  .title {
    margin-bottom: 22px;
    font-size: 22px;
    line-height: 130%;
  }
  .sub-title {
    margin-bottom: 22px;
    font-size: 19px;
    line-height: 130%;
  }
  .important {
    font-size: 17px;
    line-height: 167%;
  }
  .btn--secondary, .btn--wide {
    font-size: 20px;
  }
  .btn--wide {
    line-height: 122%;
  }
  .dot-list {
    font-size: 17px;
  }
  .dot-list li {
    margin-bottom: 15px;
  }
  .info .num-list {
    padding-left: 20px;
  }
}
.main {
  flex-grow: 1;
}

.breadcrumbs__list {
  padding: 20px 0 20px var(--padding);
  font-weight: 400;
  font-size: 12px;
  line-height: 120%;
  color: #A5A5A5;
  display: flex;
  white-space: nowrap;
  overflow-x: auto;
  -ms-overflow-style: none;
  overflow: -moz-scrollbars-none;
}
.breadcrumbs__list::-webkit-scrollbar {
  width: 0;
  height: 0;
}
.breadcrumbs__list::-webkit-input-placeholder, .breadcrumbs__list::-moz-placeholder {
  color: #fff;
}
.breadcrumbs li {
  margin-right: 10px;
  padding-right: 13px;
  position: relative;
}
.breadcrumbs li::after {
  content: "/";
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}
.breadcrumbs li:last-child {
  margin-right: 0;
}
.breadcrumbs li:last-child::after {
  display: none;
}
.breadcrumbs__link:hover, .breadcrumbs__link:focus {
  text-decoration: underline;
}
.breadcrumbs__link--active {
  color: #7E9AD0;
}

@media screen and (min-width: 1200px) {
  .breadcrumbs__list {
    padding: 24px var(--padding);
    overflow-x: visible;
    flex-wrap: wrap;
  }
}
.article {
  margin-bottom: var(--margin);
}
.article__aside {
  display: none;
}
.article__lead {
  width: 100%;
  height: 200px;
  margin-bottom: 20px;
  position: relative;
  object-fit: cover;
  position: relative;
}
.article__lead::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0.3) 100%), url(1631536522_3769.jpg);
  border-radius: 4px;
}
.article__title {
  position: absolute;
  z-index: 5;
  bottom: 20px;
  left: 16px;
  right: 16px;
}
.article__pic {
  width: 100%;
  height: 172px;
  margin-bottom: var(--margin);
  display: block;
  border-radius: 4px;
  position: relative;
}
.article__btn-big {
  margin-bottom: var(--margin);
  position: relative;
}
.article__btn-big::before, .article__btn-big::after {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  width: 20px;
  height: 1px;
  background: #BEBEBE;
}
.article__btn-big::after {
  left: auto;
  right: 0;
}
.article__btn-big .btn {
  max-width: calc(100% - 60px);
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}
.article__themes {
  margin-bottom: 20px;
  display: flex;
  flex-wrap: wrap;
}
.article__themes li {
  margin-right: 10px;
  margin-bottom: 10px;
}
.article__themes a {
  display: block;
  padding: 10px 15px;
  font-weight: 400;
  font-size: 14px;
  line-height: 120%;
  color: #5A5A5A;
  background: #EBEBEB;
  border-radius: 4px;
  transition: all 0.2s ease;
}
.article__themes a:hover, .article__themes a:focus {
  background: #E0E0E0;
}

@media screen and (min-width: 460px) {
  .article__lead {
    height: 250px;
  }
  .article__pic {
    height: 250px;
  }
}
@media screen and (min-width: 768px) {
  .article__lead {
    height: 400px;
  }
  .article__title {
    bottom: 36px;
    left: 36px;
    right: 36px;
  }
  .article__pic {
    height: 362px;
  }
  .article__btn-big::before, .article__btn-big::after {
    width: calc((100% - 410px) / 2);
  }
  .article__btn-big .btn {
    max-width: 390px;
  }
}
@media screen and (min-width: 991px) {
  .article__lead {
    height: 500px;
  }
}
@media screen and (min-width: 1200px) {
  .article__inner {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
  .article__aside {
    min-width: 270px;
    margin-left: 30px;
    display: block;
  }
  .article__main {
    max-width: 900px;
    width: calc(100% - 300px);
  }
  .article__title {
    bottom: 50px;
    right: 50px;
    left: 50px;
  }
  .article__btn-big {
    margin-bottom: 70px;
  }
  .article__themes {
    margin-bottom: 42px;
  }
  .article__themes a {
    font-size: 16px;
  }
}
.content {
  margin-bottom: var(--margin);
}
.content .num-list {
  display: none;
  padding-top: 20px;
  font-size: 12px;
}
.content .num-list p {
  display: inline;
  border-bottom: 1px dashed #C1C1C1;
}
.content__inner {
  padding: 20px;
  background: #F3F3F3;
  border-radius: 4px;
}
.content__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
}
.content__title {
  font-weight: 500;
  font-size: 15px;
  line-height: 120%;
}
.content__toggle {
  width: 18px;
  min-width: 18px;
  height: 18px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  transform: rotate(180deg);
  transition: all 0.2s ease;
}
.content__toggle--active {
  transform: rotate(0);
}

@media screen and (min-width: 768px) {
  .content__inner {
    padding: 30px;
  }
}
@media screen and (min-width: 1200px) {
  .content__title {
    font-size: 16px;
  }
  .content .num-list {
    font-size: 13px;
  }
}
.expert {
  margin-bottom: var(--margin);
}
.expert .title {
  margin-bottom: 24px;
  color: inherit;
}
.expert .btn {
  width: auto;
  padding: 14px 33px;
}
.expert__inner {
  padding: 20px 95px 40px 20px;
  background: #80A7E2;
