  border-radius: 4px;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 100;
  overflow-y: auto;
}
.modal--active {
  display: block;
}
.modal .form {
  margin-bottom: 0;
}
.modal .title {
  margin-bottom: 7px;
}
.modal .btn {
  width: 100%;
  margin-bottom: 20px;
}
.modal__lead {
  font-weight: 400;
  font-size: 13px;
  line-height: 138%;
  color: #393939;
}
.modal__top {
  margin-bottom: 20px;
  padding-right: 30px;
  position: relative;
}
.modal__close {
  position: absolute;
  right: 20px;
  top: 20px;
  z-index: 5;
}

@media screen and (min-width: 768px) {
  .modal {
    max-width: 490px;
    padding: 40px;
  }
  .modal__close {
    right: 40px;
    top: 40px;
  }
  .modal .title {
    font-weight: 500;
    font-size: 20px;
    line-height: 26px;
  }
  .modal .form__inputs {
    display: block;
  }
  .modal .btn {
    margin-bottom: 30px;
  }
  .modal__lead {
    font-size: 15px;
  }
  .modal__top {
    margin-bottom: 30px;
  }
}